var server = window.location.hostname;
var port = "1337";
var status_request = document.getElementById("status_request");
var save_button = document.getElementById('save_operation');
function save() {
    var id_graph = document.getElementById("graph").value;
    var operation_form = document.getElementById("operation").value;
    var obj = { operation_id: parseInt(operation_form), graph_id: parseInt(id_graph) };
    console.log(obj);
    fetch("http://" + server + ":" + port + "/operations/", { method: "POST", headers: {
            'Accept': 'application/json, text/plain',
            'Content-Type': 'application/json;charset=UTF-8'
        }, body: JSON.stringify(obj) })
        .then(function (response) {
        console.log(response);
        return response.json().then(function (data) { return ({ status: response.status, body: data }); });
    }).then(function (data) {
        if (data.status == 200) {
            status_request.innerHTML = "Good";
        }
        else {
            status_request.innerHTML = "Did not work out";
        }
    });
}
save_button.addEventListener('click', save);
